import { makeStyles } from '@material-ui/core/styles';

export default makeStyles((theme) => ({
  toolbar: theme.mixins.toolbar,
  title: {
    marginTop: '6%',
    fontSize:"30px"
    
  },
  emptyButton: {
    minWidth: '150px',
    [theme.breakpoints.down('xs')]: {
      marginBottom: '7px',
    },
    [theme.breakpoints.up('xs')]: {
      marginRight: '25px',
    },
  },
  checkoutButton: {
    minWidth: '140px',
  },
  link: {
    textDecoration: 'none',
  },
  cardDetails: {
    display: 'flex',
    marginTop: '8%',
    width: '100%',
    justifyContent: 'space-between',
  },
}));