import React from "react";
import { Grid } from "@material-ui/core";
import Product from "./Product/Product";
import useStyles from "./styles";

const Products = ({ products, onAddCart }) => {
  const classes = useStyles();
  // const products=[
  //     {id:1 , name:'misak eefa' , description:'50x50 keramogranit ', price:'7$',
  //     image:'https://sc01.alicdn.com/kf/HTB1TDsst7KWBuNjy1zjq6AOypXa0/200194972/HTB1TDsst7KWBuNjy1zjq6AOypXa0.jpg'},
  //     {id:2 , name:'eraz' , description:'60x60 keramogranit', price:'9$',
  //     image:'https://5.imimg.com/data5/BC/AA/MY-10100535/60x60-cm-glazed-porcelain-tiles-500x500.jpg'}
  // ]
  return (
    <main className={classes.content}>
      <div className={classes.toolbar} />
      <Grid container justify="center" spacing={4}>
        {products.map((product) => (
          <Grid item key={product.id} xs={12} sm={6} md={4} lg={3}>
            <Product product={product} onAddCart={onAddCart} />
          </Grid>
        ))}
      </Grid>
    </main>
  );
};

export default Products;
